:root{
  /* kleur variabelen instellen */
  --color-primary: #E97451;
  --color-secondary: #3F4652;
  --color-selected: #C36447;
  --color-light: #EBECED;
}

@font-face {
  font-family: "Rancho";
  src: url('./fonts/Rancho-Regular.ttf');
}

* {
  padding: 0px;
  margin: 0px;
}
body {
  /* background-image: url("./img/bg.jpg"); */
  background-size: cover;
}

header {
  display: flex;
  width: calc(100% - 80px);
  height: 75px;
  background-color: var(--color-primary);
  align-items: center;
  justify-content: space-between;
  padding: 0px 40px;
}

.title {
  font-family: "Rancho";
  font-size: 48px;
  color: var(--color-light);
}

.selected {
  background-color: var(--color-selected);
}

ul {
  display: flex;
  justify-content: space-between;
}

li {
  height: 50px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;

  padding-left: 10px;
  padding-right: 10px;
  margin-right: 20px;
}

li:hover {
  background-color: var(--color-selected);
  cursor: pointer;
}

li a {
  color: var(--color-light);
  font-family: "Rancho";
  text-decoration: none;
  font-size: 48px;
}

.icon {
  width: 48px;
  height: 48px;
  background-size: cover;
  margin-right: 10px;
}


main {
  width: 100%;
  height: calc(100vh - 75px);
  display: flex;
  align-items:center;
  justify-content: center;
}

main div {
  display: block;
}

.banner {
  display: block;
  font-size: 64px;
  font-family: "Rancho";
  color: var(--color-light);
}

.btn {
  width: 450px;
  height: 75px;
  background-color: var(--color-primary);
  color: var(--color-light);
  font-family: "Rancho";
  font-size: 48px;
  border-radius: 10px;
  border: none;
  margin-top: 60px;
  cursor: pointer;
}

.btn:hover {
  background-color: var(--color-selected);
}

.text {
  background-color: var(--color-secondary);
  color: var(--color-light);
  padding: 20px;
  border-radius: 10px;
  font-size: 32px;
  font-family: "Rancho";
  margin-bottom: 40px;

}

.text div {
  display: flex;
  justify-content: space-between;
}

.text div div {
  display: block;
}

.cards {
  display: flex;
  justify-content: space-between;
}

.card {
  width: 120px;
  height: 120px;
  background-color: var(--color-light);
  border-radius: 10px;
  color: var(--color-light);
  font-family: "Rancho";
  font-size: 28px;

  display: flex;
  justify-content: end;
  align-items: flex-end;
  padding: 15px;
}

.card:nth-child(1) {
  /* background-image: url("../img/avatar/avatar1.png"); */
  background-size: cover;
}
.card:nth-child(2) {
  /* background-image: url("../img/avatar/avatar2.png"); */
  background-size: cover;
}
.card:nth-child(3) {
  /* background-image: url("../img/avatar/avatar3.png"); */
  background-size: cover;
}
.card:nth-child(4) {
  /* background-image: url("../img/avatar/avatar4.png"); */
  background-size: cover;
}
.card:nth-child(5) {
  /* background-image: url("../img/avatar/avatar5.png"); */
  background-size: cover;
}

input[type="number"] {
  height: 46px;
  width: 46px;
  font-size: 24px;
}

input[type="date"] {
  height: 46px;
  font-size: 18px;
}

.btn-small {
  background-color: var(--color-light);
  color: var(--color-secondary);
  padding: 10px;
  width: 50px;
  height: 50px;
  font-size: 24px;
  cursor: pointer;
}

.btn-small:nth-child(1) {
  border-radius: 10px 0px 0px 10px;
  border: none;
}

.btn-small:nth-child(3) {
  border-radius: 0px 10px 10px 0px;
  border: none;
}

.maxWidth {
  max-width: 770px;
}